	methods: {
		getRandomIntInclusive(min, max) {
			min = Math.ceil(min);
			max = Math.floor(max);
			return Math.floor(Math.random() * (max - min + 1)) + min; //含最大值，含最小值 
		},

		linkChat(item) {
			uni.navigateTo({
				url: './chat?oppositeinfo=' + JSON.stringify(item),
				animationDuration: 300,
				animationType: 'slide-in-bottom'
			})
		},

		loadChatList(type) {
			//type add 加载更多 refresh下拉刷新
			if (type === 'add') {
				if (this.loadMoreStatus === 2) {
					return;
				}
				this.showLoadMore = true;
				this.loadMoreStatus = 1;
			}





			//setTimeout模拟异步请求数据
			setTimeout(() => {
				if (type === 'refresh') {
					let tempList = this.chatList.length <= 12 ? this.chatList.reverse() : this.chatList.splice(this.getRandomIntInclusive(
						0, 12), 12);
					//刷新前清空数组
					this.chatList = [];
					this.chatList = tempList;
				}

				//下拉刷新 关闭刷新动画
				if (type === 'refresh') {
					this.$refs.mixPulldownRefresh && this.$refs.mixPulldownRefresh.endPulldownRefresh();



					this.loadMoreStatus = 0;
				}
				//上滑加载 处理状态
				if (type === 'add') {
					this.chatList = [...this.chatList, ...this.chatList];
					this.loadMoreStatus = this.chatList.length > 50 ? 2 : 0;
					this.showLoadMore = false;
				}
			}, 2000)
		},
		onPulldownReresh() {
			this.loadChatList('refresh');
		},
		loadMore() {
			this.loadChatList('add');
		},
		setEnableScroll(enable) {
			// if (this.enableScroll !== enable) {
			// 	this.enableScroll = enable;
			// }
		},
		// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		ListTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		ListTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		}
	}
